* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}
::placeholder {
  color: #939393;
}
body {
  background-color: #fff;
  color: #000;
  font-family: "Helvetica", Arial, sans-serif;
  overflow-x: hidden;
  overflow-y: visible;
  position: relative;
  height: 100vh;
  width: 100%;
}
body.fancybox-active {
  overflow-y: hidden;
}
body .allContent,
body .formPage {
  height: auto;
  width: 100%;
}
input,
textarea {
  -webkit-appearance: none;
}
input[hidden],
button[hidden],
textarea[hidden] {
  display: none;
}
h1 {
  font-size: 4.8rem;
  line-height: normal;
  margin: 0;
}
h2 {
  font-size: 3.6rem;
  line-height: normal;
  margin: 0;
}
h3 {
  font-size: 3.2rem;
  line-height: normal;
  margin: 0;
}
h4 {
  font-size: 3rem;
  letter-spacing: -0.08rem;
  line-height: normal;
  margin: 0;
}
h5 {
  font-size: 2.4rem;
  letter-spacing: -0.05rem;
  line-height: normal;
  margin: 0;
}
h6 {
  font-size: 2rem;
  letter-spacing: 0;
  line-height: normal;
  margin: 0;
}
p {
  font-size: 1.6rem;
  line-height: normal;
  margin: 0;
}
p.red {
  color: #b8181c;
}
p span {
  font-size: 1.6rem;
  line-height: normal;
}
span {
  font-size: 1.2rem;
  margin: 0;
}
span.red {
  color: #b8181c;
}
a {
  color: #000;
}
a.red {
  color: #b8181c;
}
a:hover {
  opacity: 0.8;
  color: #b8181c;
}
section {
  margin-bottom: 0.4rem;
  padding-bottom: 3.2rem;
}
input[type='color']:focus,
input[type='date']:focus,
input[type='datetime']:focus,
input[type='datetime-local']:focus,
input[type='email']:focus,
input[type='month']:focus,
input[type='number']:focus,
input[type='password']:focus,
input[type='search']:focus,
input[type='tel']:focus,
input[type='text']:focus,
input[type='url']:focus,
input[type='week']:focus,
input:not([type]):focus,
textarea:focus,
select:focus {
  border-color: #b8181c;
}
input[type='color']:hover,
input[type='date']:hover,
input[type='datetime']:hover,
input[type='datetime-local']:hover,
input[type='email']:hover,
input[type='month']:hover,
input[type='number']:hover,
input[type='password']:hover,
input[type='search']:hover,
input[type='tel']:hover,
input[type='text']:hover,
input[type='url']:hover,
input[type='week']:hover,
input:not([type]):hover,
textarea:hover,
select:hover {
  box-shadow: 0px 0px 0.5rem 0px #939393;
}
.container {
  max-width: 115.2rem;
  padding: 0;
}
.container.inwards {
  width: 74rem;
}
.sectionTitle {
  margin: 3.2rem 0;
  width: 100%;
}
.sectionTitle__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.sectionTitle__top > :first-child {
  flex: 1;
  padding-right: 2.4rem;
}
.sectionTitle__top > :last-child {
  flex: none;
}
.sectionTitle__bottom {
  margin-top: 1.6rem;
}
.sectionTitle__description {
  font-size: 2rem;
  line-height: 2.8rem;
  color: #3a3a3a;
  width: 75%;
}
.sectionTitle h1,
.sectionTitle h2,
.sectionTitle h3,
.sectionTitle h4,
.sectionTitle h5,
.sectionTitle h6 {
  padding-top: 0.4rem;
}
.sliderNav {
  flex: none;
  display: grid;
  grid-template-columns: 4.8rem 4.8rem;
  grid-column-gap: 1.6rem;
}
.sliderNav .slick-arrow {
  position: relative;
}
.bb {
  border-bottom: 1px solid #e6e6e6;
}
.row {
  width: 100%;
  margin-left: 0;
}
.row .column {
  padding: 0 1.6rem;
}
.page-title {
  margin-bottom: 4.8rem;
}
.textBlock p {
  line-height: 2.4rem;
}
.seeMore {
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  display: flex;
}
.play {
  height: 4.8rem;
  width: 4.8rem;
  border-radius: 50%;
  font-size: 6.4rem;
  overflow: hidden;
  position: absolute;
  top: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  line-height: 1;
  z-index: 1;
  box-shadow: 0 0.4rem 2.4rem rgba(0,0,0,0.56);
}
.play:hover {
  color: #b8181c;
  background-color: #fff;
}
.hide {
  display: none;
}
.show {
  display: block;
}
.top-container > :first-child {
  background-color: #193c6d;
  padding: 1.2rem 0;
}
.top-container > :first-child .row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.top-container > :first-child .row > :first-child {
  display: flex;
  align-items: center;
}
.top-container > :first-child .row > :first-child a {
  margin: 0 1.6rem;
}
.top-container > :first-child .row > :first-child > :first-child {
  margin-left: 0;
}
.top-container > :first-child .row > :first-child > :last-child {
  margin-right: 0;
}
.top-container > :first-child .row > :last-child {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.top-container > :first-child a {
