a[hidden],
input[type="button"][hidden],
input[type="submit"][hidden],
button[hidden] {
  opacity: 0.4;
  pointer-events: none;
}
a.btn,
input[type="button"].btn,
input[type="submit"].btn,
button.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 4.8rem;
  border-radius: 0.4rem;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.6rem;
  padding: 1.6rem 2.4rem;
  cursor: pointer;
  text-align: center;
  transition: 0.25s linear;
