.links__container {
  display: grid;
  grid-template-columns: 25% 75%;
  margin-top: 4rem;
  width: 100%;
}
.links__container > :first-child {
  padding-right: 1.6rem;
  padding-left: 0;
}
.links__container > :last-child {
  padding-right: 0;
  padding-left: 1.6rem;
}
.links__container > :last-child > :first-child {
  display: grid;
  grid-template-columns: 1fr 2fr;
}
.links__container > :last-child > :first-child > :first-child {
  padding-right: 1.6rem;
}
.links__container > :last-child > :first-child > :last-child {
  padding-left: 1.6rem;
}
.links__container > :last-child > :first-child > :last-child > :first-child {
  display: grid;
  grid-template-columns: calc(50% - 1.6rem) calc(50% - 1.6rem);
  grid-column-gap: 3.2rem;
  grid-row-gap: 4rem;
}
.links__container .partition li {
  line-height: 1;
  margin-bottom: 1.6rem;
  transition: 0.25s ease-in-out;
}
.links__container .partition a {
